


































import BASEURL from "../../common/baseInfo.js";
import uniPopup from "../../components/uni-popup.vue";
export default {
	data() {
		return {
			userInfo:{},
			openId: null,
			money: null,
			popType: 'middle',
			showPopupMiddle: false,
		};
	},
	onLoad() {
		let that = this;
		uni.getStorage({
			key: 'userInfo',
			success: function (res) {
				// console.log(JSON.parse(res));
				console.log(res.data);
				that.userInfo = res.data;
			}
		})
		uni.getStorage({
			key: 'openIdInfo',
			success: function(res) {
				// console.log(JSON.parse(res));
				console.log(res);
				that.openId = res.data;
				that.getUserAppInfo();
			}
		})
	},
	components: {
		uniPopup
	},
	methods:{
		getUserAppInfo() {
			let that = this;
			uni.request({
				url: `${BASEURL}/khxcx-0.0.1-SNAPSHOT/findUserByOpenId.action`,
				method: 'GET',
				data: {
					openId: that.openId
				},
				success: res => {
					console.log(res.data.data.money);
					that.money = res.data.data.money;
				},
				fail: (err) => {
					console.log(err);
				},
				complete: () => {}
			});
		},
		//统一的关闭popup方法
		hidePopup: function() {
			this.showPopupMiddle = false;
			this.showPopupTop = false;
			this.showPopupBottom = false;
		},
		getMoney() {
			this.hidePopup();
			this.popType = 'middle';
			this.showPopupMiddle = true;
		},
		getAll() {
			let that = this;
			uni.request({
				url: `${BASEURL}/khxcx-0.0.1-SNAPSHOT/insertDiscountRecord.action?openId=${that.openId}`,
				method: 'POST',
				data: {
					// openId: that.openId
				},
				success: res => {
					console.log(res);
					uni.showToast({
						title: '提现申请发起成功',
						icon: 'success',
						mask: true,
						duration: 2000
					});
					that.getUserAppInfo();
					setTimeout(() => {
						that.hidePopup();
					},2000)
				},
				fail: () => {},
				complete: () => {}
			});
		}
	}
	
}
